import { Plot } from '../../core/plot';
import { deepAssign } from '../../utils';
import { Adaptor } from '../../core/adaptor';
import { FunnelOptions } from './types';
import { adaptor } from './adaptor';

export { FunnelOptions };

export class Funnel extends Plot<FunnelOptions> {
  /**  */
  public type: string = 'funnel';

  /**
   *   
   */
  protected getDefaultOptions(): Partial<FunnelOptions> {
    return deepAssign({}, super.getDefaultOptions(), {
      // annotation  chart appendPadding hack, 
      appendPadding: [0, 50],
      label: {
        offset: 0,
        position: 'middle',
        // layout: {
        //   type: 'limit-in-shape',
        // },
        style: {
          fill: '#fff',
          fontSize: 12,
        },
        callback: (xField, yField) => ({
          content: `${xField} ${yField}`,
        }),
      },
      tooltip: {
        showTitle: false,
        showMarkers: false,
        shared: false,
      },
      conversionTag: {
        offsetX: 10,
        offsetY: 0,
        style: {},
        formatter: (datum) => `${(datum.$$percentage$$ * 100).toFixed(2)}%`,
      },
    });
  }

  /**
   *   
   */
  protected getSchemaAdaptor(): Adaptor<FunnelOptions> {
    return adaptor;
  }
}
